export default {
	data() {
		return {
			input:'',//用户输入运单号
			inputoff:false,
			paging:0,//控制代收发货页面
			pageNum:1,
			scrollLeft: 0,
			isClickChange: false,
			currentTab: 0,
			menuTabs: [
				{
					name: '全部'
				},
				{
					name: '未发货'
				},
				{
					name: '已发货'
				},
				{
					name: '交易完成'
				},
				{
					name: '异常关闭'
				}
			],
			menuLists:null,//加载订单
			userlist: '' //用户缓存
		};
	},
	onLoad() {
		this.userlist = uni.getStorageSync('userlist'); //加载用户缓存
		console.log(this.userlist);
		this.judgeOrder();
	},
	methods: {
		//下拉刷新
		onPullDownRefresh() {
			console.log('下拉刷新');
			this.swichMenu(this.currentTab);
				this.utils.success('刷新成功！',()=>{
					uni.stopPullDownRefresh();
				});
		},
		//触底加载更多
		onReachBottom() {
			if (this.menuLists==null) {
				return;
			} else{
				if (this.paging==0) {
				this.judgeOrder();
				} else{
					this.abnormalOrder(this.currentTab);
				}
			}
			
		},
		// 更新用户信息
		updateUser: function(data) {
			// 判断是否登录
			this.utils.showloading();
			this.http
				.getApi('/user/login', { storeLogin: 1, storeOpendid: data }, 'post', '')
				.then(res => {
					uni.setStorageSync('userlist', res.data);
					this.judgeOrder();
					this.utils.success('刷新成功！',()=>{
						uni.hideLoading();
					});
				})
				.catch(err => {
				});
		},
		// 确认收货
		Confirm(e){
			// let e = e;
			this.utils.showloading();
			this.http.getApi('/order/takeOver', {userId:this.userlist.id, orderNo: e }, 'get', this.userlist.storeOpendid)
			.then(res => {
				// this.menuLists = res.data.list;
				console.log(res);
				this.utils.success('操作成功！',()=>{
					this.updateUser(this.userlist.storeOpendid);
				});
				
			})
			.catch(err => {
				uni.hideLoading();
				console.log(err);
			});
		},
		onKeyInput: function(event) {
		    this.input = event.target.value
			console.log(this.input);
		},
		
		Orderdetails(item){
			console.log(item);
			this.doUrl('pages/index/orderDetails',item);
		},
		delivery(){
			this.inputoff = true;
			// let e = e;
			uni.showModal({
			    title: '请填写快递运单号',
			    content: '请在输入框处填写快递运单号',
			    success: function (res) {
			        if (res.confirm) {
			            console.log('用户点击确定');
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
			
		},
		delivery2(e){
			this.utils.showloading();
			this.http.getApi('/order/delivery', {orderNo:e, wuliuNo: this.input }, 'get', this.userlist.storeOpendid)
			.then(res => {
				// this.menuLists = res.data.list;
				console.log(res);
				this.utils.success('操作成功！',()=>{
					uni.hideLoading();
					this.inputoff = false;
					this.updateUser(this.userlist.storeOpendid);
				})
			})
			.catch(err => {
				uni.hideLoading();
				console.log(err);
				this.utils.success('该订单已操作成功！',()=>{
					uni.hideLoading();
					this.inputoff = false;
					this.updateUser(this.userlist.storeOpendid);
				})
			});
		},
		// 获取全部进订单
		judgeOrder() {
			this.utils.showloading();
			if (this.paging==0) {
				this.http.getApi('/order/getmyorder', { myId: this.userlist.id, pageNum: this.pageNum, pageSize: 10 }, 'get', this.userlist.storeOpendid)
				.then(res => {
					uni.hideLoading();
					// this.menuLists = res.data.list;
					console.log(res);
					if (res.data.list.length==0) {
						// this.menuLists = null;
						if (this.pageNum==1) {
							this.menuLists = null;
						} else{
							this.utils.error('暂无更多')
							console.log('------暂无更多------');
						}
					} else{
						this.menuLists = this.pageNum>1 ? this.menuLists.concat(res.data.list) : res.data.list;
						this.pageNum = res.data.pageNum == this.pageNum ? this.pageNum+1 : this.pageNum;
					}
				})
				.catch(err => {
					uni.hideLoading();
					console.log(err);
				});
			} else{
				this.http.getApi('/order/getorder', { myId: this.userlist.id, pageNum: this.pageNum, pageSize: 10 }, 'get', this.userlist.storeOpendid)
					.then(res => {
						uni.hideLoading();
						// this.menuLists = res.data.list;
						console.log(res);
						if (res.data.list.length==0) {
							// this.menuLists = null;
							if (this.pageNum==1) {
								this.menuLists = null;
							} else{
								this.utils.error('暂无更多')
								console.log('------暂无更多------');
							}
						} else{
							this.menuLists = this.pageNum>1 ? this.menuLists.concat(res.data.list) : res.data.list;
							this.pageNum = res.data.pageNum == this.pageNum ? this.pageNum+1 : this.pageNum;
						}
					})
					.catch(err => {
						uni.hideLoading();
						console.log(err);
					});
			}
			
		},
		//获取其他订单
		abnormalOrder(current) {
			let com = current;
			this.utils.showloading();
			if (this.paging==0) {
				this.http.getApi('/order/getmyorderStusas', { myId: this.userlist.id, status: com - 1, pageNum: this.pageNum, pageSize: 10 }, 'get', this.userlist.storeOpendid)
				.then(res => {
					uni.hideLoading();
					console.log(res);
					if (res.data.list.length==0) {
						// this.menuLists = null;
						if (this.pageNum==1) {
							this.menuLists = null;
						} else{
							this.utils.error('暂无更多')
							console.log('------暂无更多------');
						}
					} else{
						this.menuLists = this.pageNum>1 ? this.menuLists.concat(res.data.list) : res.data.list;
						this.pageNum = res.data.pageNum == this.pageNum ? this.pageNum+1 : this.pageNum;
					}
				})
				.catch(err => {
					uni.hideLoading();
					console.log(err);
				});
			} else{
				if (this.paging==1) {
					if(current==1){
						this.http.getApi('/order/statusZero', { myId: this.userlist.id, pageNum: this.pageNum, pageSize: 10 }, 'get', this.userlist.storeOpendid)
						.then(res => {
							uni.hideLoading();
							console.log(res);
							if (res.data.list.length==0) {
								if (this.pageNum==1) {
									this.menuLists = null;
								} else{
									this.utils.error('暂无更多')
									console.log('------暂无更多------');
								}
							} else{
								this.menuLists = this.pageNum>1 ? this.menuLists.concat(res.data.list) : res.data.list;
								this.pageNum = res.data.pageNum == this.pageNum ? this.pageNum+1 : this.pageNum;
							}
						})
						.catch(err => {
							uni.hideLoading();
							console.log(err);
						});
					}
					if(current==2){
						this.http.getApi('/order/statusOne', { myId: this.userlist.id, pageNum: this.pageNum, pageSize: 10 }, 'get', this.userlist.storeOpendid)
						.then(res => {
							uni.hideLoading();
							console.log(res);
							if (res.data.list.length==0) {
								if (this.pageNum==1) {
									this.menuLists = null;
								} else{
									this.utils.error('暂无更多')
									console.log('------暂无更多------');
								}
								
							} else{
								this.menuLists = this.pageNum>1 ? this.menuLists.concat(res.data.list) : res.data.list;
								this.pageNum = res.data.pageNum == this.pageNum ? this.pageNum+1 : this.pageNum;
							}
						})
						.catch(err => {
							uni.hideLoading();
							console.log(err);
						});
					}
					if(current==3){
						this.http.getApi('/order/statusTwo', { myId: this.userlist.id, pageNum: this.pageNum, pageSize: 10 }, 'get', this.userlist.storeOpendid)
						.then(res => {
							uni.hideLoading();
							console.log(res);
							if (res.data.list.length==0) {
								if (this.pageNum==1) {
									this.menuLists = null;
								} else{
									this.utils.error('暂无更多')
									console.log('------暂无更多------');
								}
							} else{
								this.menuLists = this.pageNum>1 ? this.menuLists.concat(res.data.list) : res.data.list;
								this.pageNum = res.data.pageNum == this.pageNum ? this.pageNum+1 : this.pageNum;
							}
						})
						.catch(err => {
							uni.hideLoading();
							console.log(err);
						});
					}
					else{
						this.menuLists=null;
						this.utils.success('暂无订单！',()=>{
							uni.hideLoading();
						})
					}
				} else{
					this.menuLists=null;
					this.utils.success('暂无订单！',()=>{
						uni.hideLoading();
					})
				}
					
				
				
			}
		},

		swichMenu: async function(current) {
			//点击其中一个选项
			if (this.currentTab == current) {
				return false;
			} else {
				if (current == 0) {
					this.currentTab = current;
					this.pageNum = 1;
					this.judgeOrder();
				} else {
					this.currentTab = current;
					this.pageNum = 1;
					this.abnormalOrder(current);
				}
			}
		},
		purchase(){
			this.paging = 0;
			this.pageNum = 1;
			this.judgeOrder();
		},
		Shipment(){
			this.paging = 1;
			this.pageNum = 1;
			this.judgeOrder();
		}
	}
};
